
#resumePreview {
  background: #444;
  height: 100%;
  -webkit-box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
          box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.25);
  padding: 1em;
  color: #333;
  overflow: hidden;
  border-radius: 6px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#resumePreview * {
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    font-variant: normal;
    font-weight: normal;
}
#resumePreview .content {
    position: relative;
    background: #FDFDFC;
    width: 99%;
    height: 100%;
    overflow: hidden;
    border-radius: 6px;
}
#resumePreview .content > .previewHeader {
      font-weight: bold;
      margin-top: 50px;
      text-align: center;
}
#resumePreview .content > .previewHeader > h1 {
        font-size: 60px;
        letter-spacing: 20px;
        color: #f3f0e8;
        margin: 0;
}
#resumePreview .content > .previewHeader > p {
        font-size: 13px;
        color: #3B3C40;
}
#resumePreview .content > .previewContent {
      padding: 10px 64px;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      max-height: 80%;
      -webkit-box-orient: vertical;
      -webkit-box-direction: normal;
          -ms-flex-direction: column;
              flex-direction: column;
      overflow: auto;
}
#resumePreview .content > .previewContent section {
        margin-top: 50px;
}
#resumePreview .content > .previewContent section ul {
          list-style: none;
}
#resumePreview .content > .previewContent section li > hr {
          margin-top: 10px;
}
#resumePreview .content > .previewContent section h3 {
          border-bottom: 1px solid #999;
          padding-bottom: .3em;
          margin-bottom: .3em;
          color: #69bcc2;
}
#resumePreview .content > .previewContent section p {
          margin-top: 10px;
          color: #3B3C40;
}
#resumePreview .content .exitPreview {
      float: right;
      margin: 16px 16px 0 0;
      width: 2em;
